import React, { Component } from "react";
import "./App.css";
import AddTodo from "./components/AddTodo";
import VisibleTodoList from "./components/VisibleTodoList";
import Footer from "./components/Footer";

class App extends Component {
  state = {
    nextTodoId: 3,
    visibilityFilter: 0, // 0 :SHOW_ALL,1:Active,2:Completed
    todos: [
      { id: 1, completed: false, text: "123" },
      { id: 2, completed: false, text: "222" },
      { id: 3, completed: false, text: "333" },
    ],
  };

  handleAddTodo = (value) => {
    let { nextTodoId, todos } = this.state;
    todos.push({
      id: ++nextTodoId,
      completed: false,
      text: { value }.value,
    });
    this.setState({ todos, nextTodoId });
  };

  handleClick = (id) => {
    const todos = this.state.todos.map((todo) => {
      if (todo.id === id) {
        todo.completed = !todo.completed;
      }
      return todo;
    });
    this.setState({ todos });
  };

  handleDelClick = (id) => {
    // console.log("点到了 ", id);
    const todos = this.state.todos.filter((todo) => todo.id !== id);
    // console.log(todos);
    this.setState({ todos });
  };

  handleFooterClick = (id) => {
    // console.log("点到了 ", id);
    const visibilityFilter = id;
    this.setState({ visibilityFilter });
  };

  render() {
    return (
      <div className="container">
        <AddTodo addTodo={this.handleAddTodo} />
        <VisibleTodoList
          todos={this.state.todos}
          visibilityFilter={this.state.visibilityFilter}
          onClick={this.handleClick}
          delClick={this.handleDelClick}
        />
        <Footer
          onClick={this.handleFooterClick}
          visibilityFilter={this.state.visibilityFilter}
        />
      </div>
    );
  }
}

export default App;
